import {address} from '../../../common/getlocation.js'
var QQMapWX = require('../../../common/qqmap-wx-jssdk.js');
var qqmapsdk = new QQMapWX({
	key: 'CWUBZ-627RK-ZDIJR-APUZX-IGZQS-PNFWB'
});
export default {
	data() {
		return {
			clearshow:false,
			pageshow:true,
			nowcity:'',
			hotcitylist:[],
			hotSelectCity:[
				{name:'成都市'},
				{name:'达州市'},
				{name:'重庆市'},
				{name:'三亚市'},
				{name:'大理白族自治州'}
			],
			keywords:'',
			route:false
		}
	},
	methods: {
		showData(){
			this.clearshow=true
			this.pageshow=false
		},
		//获取定位
		getlocation(){
			address().then(res=>{
				this.nowcity=res.result.ad_info.city
			}).catch(err=>{
				this.nowcity='北京市'
			})
		},
		//获取当前城市定位
		clickCity(){
			this.$store.commit('citymuta',this.nowcity)
			uni.switchTab({
				url:`/pages/strategy/strategy`
			})
		},
		//选中热门城市
		selectHotCity(city){
			if(!this.route){
				this.$store.commit('citymuta',city)
			}
			else{
				this.$store.commit('pulishmuta',city)
			}
			uni.navigateBack({
				delta:1
			})
		},
		//实时搜索城市
		searchCity(e){
			qqmapsdk.getSuggestion({
				keyword:e.detail.value,
				//分类
				// filter:'category=景点',
				success:res=>{
					let city=res.data.map(item=>{
						return item.title
					})
					let firstcity=res.data[0].city
					this.hotcitylist=[firstcity,...city]
				},
				fail:err=>{
					console.log(err)
				}
			})
		},
		//选中搜索城市地名
		checkCity(e){
			if(!this.route){
				this.$store.commit('citymuta',e)
			}
			else{
				this.$store.commit('pulishmuta',e)
			}
			uni.navigateBack({
				delta:1
			})
		},
		deleteKey(){
			this.keywords=''
			this.hotcitylist=[]
		}
	},
	created() {
		this.getlocation()
	},
	//判断路由
	onLoad() {
		let pages=getCurrentPages()
		let prevpage=pages[pages.length-2]
		console.log(prevpage.route)
		if(prevpage.route==='pages/strategy/publish/publish'){
			this.route=true
		}
	}
}
